body,
html,
#root {
  height: 100%;
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}

.col-esq {
  height: 100%;
  width: 15%;
  /* background-color: rgba(10, 10, 10, 0.75) ; */
  background-color: rgba(37, 47, 186, 0.75) ;
  z-index: 2;
  position: absolute;
  overflow-y: auto;
}

.header-colEsq {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  padding-bottom: 5px;
  color: white;
  font-size: large;
  font-weight: bolder;
  border-bottom: 2px solid white;
}

.div-sessao{
  display: flex;
  flex-direction: column;
}

.div-sessao .linha-colEsq{
  width: 100%;
}

.checkbox{
  margin-left: 10px;
}

label{
  font-weight: bold;
  color: white;
  font-size: large;
}

.div-sessao{
  width: 100%;
  display: flex;
  align-items: center;
}

.titulo-sessao p{
  font-weight: bold;
  font-size: large;
  color: white;
  margin-bottom: 0;
}

.linha-colEsq {
  padding-bottom: 5px;
  padding-top: 5px;
  display: flex;
}

.botao-esq {
  cursor: pointer;
  /* border: none;
  border-radius: 3px; */
  padding: 5px;
  width: 100%;
  color:  rgba(37, 47, 186, 0.75);
  font-weight: bold;
  font-size: medium;
}

.col-principal {
  height: 100%;
  width: 100%;
}

::-webkit-scrollbar-track {
  background-color: rgba(82, 37, 186, 0.75);
}
::-webkit-scrollbar {
  width: 3px;
  background-color: rgba(82, 37, 186, 0.75);
}
::-webkit-scrollbar-thumb {
  background: white;
}